.control {
  opacity: 0.8;
  padding-left: 26px;
  position: relative;
}

.control__label {
  font-size: 14px;
  line-height: 15.68px;
}

.control__mark {
  width: 15px;
  height: 15px;
  border: 1px solid $color-default-white;
  border-radius: 2px;
  position: absolute;
  left: 0;
}

.control__input:checked + .control__mark {
  background: url("../img/svg/checkbox.svg") no-repeat 2px 2px;
}
